define('../src/journeys/j113-epass/j113-epass',['jquery', 'select2', 'pickerdate', 'parsley'], function($, select2, parsley) {

    'use strict';

    var j113_epass = function($journey) {

        this.$journey = $journey;

        return this;
    };

    j113_epass.prototype.init = function() {
        var _this = this;

        this.loadMultiplier = 1;

        if ($('.j113-epass--permPass-green').length > 0) {
            _this.initlaunchBar($('.j113-epass--permPass-bar'));
        };

        $('.form.hidden').each(function() {
            $(this).hide();
            if ($(this).hasClass($('.j113-epass--permPass__mode').find('input:checked').val())) {
                $(this).show();
            };
        });

        $('.j113-epass--permPass__mode').find('input').off('click.mode').on('click.mode', function() {
            $('.form.hidden').each(function() {
                $(this).hide();
                $(this).find('input, select').each(function() {
                    $(this).parsley().reset();
                    $(this).parent().removeClass('form-field__input-wrapper--error');
                })
                if ($(this).hasClass($('.j113-epass--permPass__mode').find('input:checked').val())) {
                    $(this).show();
                };
            });
        });

        this.multiPass();
        this.multiPassMobile();
        //this.sharePass();

        $('.j113-epass-sharelink').off('click.share').on('click.share', function() {
            $('.j113-epass-sharemodal').find('.m39-modal__trigger').click()
        });

        $('.j113-epass--multi_attachment').find('button[type="submit"]').off('click.check').on('click.check', function(event) {
            _this.checkFields(event, true);
        });

        $('.j113-epass--multi_attachment').find('.form-field__input').off('focusout.113').on('focusout.113, change', function() {
            _this.checkFields(null, false);
        });

        //$('.j113-epass-shareradios').find('.form-field__input--radio').each(function () {
        //    $(this).off('click.share').on('click.share', function () {
        //        _this.sharePass();
        //    })
        //});

        $(window).off('listbinding').on('listbinding', function() {
            _this.multiPass();
            _this.multiPassMobile();
        });

        $('.j113-epass--table_filter').off('click.filter').on('click.filter', function() {
            var $fil = $(this);

            if (!$fil.hasClass('ascending') && !$fil.hasClass('descending')) {
                $fil.addClass('ascending');
            } else if ($fil.hasClass('ascending') && !$fil.hasClass('descending')) {
                $fil.removeClass('ascending');
                $fil.addClass('descending');
            } else {
                $fil.removeClass('ascending');
                $fil.removeClass('descending');
            }
        });

        if (this.$journey.find('.j113-loadmore-card').length > 0) {
            _this.$journey.find('.j113-loadmore-card').each(function() {
                _this.loadMore(this, true);
            });

            $(window).off('resize.loadmore').on('resize.loadmore', function() {
                setTimeout(function() {
                    _this.$journey.find('.j113-loadmore-card').each(function() {
                        _this.loadMore(this, false);
                    })
                }, 250);
            });
        };

        this.$journey.find('.j113-epass--table_cell--modaltrigger').off('click.modal').on('click.modal', function() {
            var target = $(this).attr('data-modaltrigger');

            if ($(this).closest('.m25-tabs__content').find('.m39-modal__trigger[data-modaltrigger="' + target + '"]') != undefined) {
                $(this).closest('.m25-tabs__content').find('.m39-modal__trigger[data-modaltrigger="' + target + '"]').trigger('click');
            }
        });

        $(window).off('j113_epass_reinit').on('j113_epass_reinit', function () {
            _this.init();
        })
    };

    j113_epass.prototype.PermPassPaginator = function() {
        var _this = this,
            $button = $('.j113-epass--permPass__form--button'),
            $backbutton = $('.j113-epass--permPass__form--button_back'),
            page,
            step;

        $button.off('click.page').on('click.page', function() {
            var good = 0;

            page = $(this).attr('data-page');
            step = $('.j113-epass--permPass__form-page' + (parseInt(page) + 1).toString() + '.' + $('.j113-epass--permPass__mode').find('input:checked').val()).attr('data-desc')

            $('.j113-epass--permPass__form-page' + page + '.' + $('.j113-epass--permPass__mode').find('input:checked').val()).find('.form-field__input').each(function() {
                if ($(this).parsley().validate() == true) {

                } else {
                    good++;
                };
            });

            if (good == 0) {
                $('.j113-epass--permPass__form-page' + page + '.' + $('.j113-epass--permPass__mode').find('input:checked').val()).addClass('hidden');
                $('.j113-epass--permPass__form-page' + (parseInt($(this).attr('data-page')) + 1 + '.' + $('.j113-epass--permPass__mode').find('input:checked').val()).toString()).removeClass('hidden');
                $('.m38-step-tracker').attr('data-current-step', (parseInt($(this).attr('data-page')) + 1).toString());
                $('.m38-step-tracker__progressbar').attr('aria-valuetext', step);
                jQuery(window).trigger('reinit_m38');
                $('.m13-footer-back-to-top__link').trigger('click');
            };
        });

        $backbutton.off('click.page').on('click', function() {

            step = $('.j113-epass--permPass__form-page' + (parseInt(page)).toString() + '.' + $('.j113-epass--permPass__mode').find('input:checked').val()).attr('data-desc')

            $('.j113-epass--permPass__form-page' + page + '.' + $('.j113-epass--permPass__mode').find('input:checked').val()).find('.form-field__input').each(function() {
                $(this).parsley().reset();
                $(this).removeClass('form-field__input--error');
                $(this).parent().removeClass('form-field__input-wrapper--error');
            });

            var backpage = $(this).attr('data-page')

            $('.j113-epass--permPass__form-page' + backpage + '.' + $('.j113-epass--permPass__mode').find('input:checked').val()).addClass('hidden');
            $('.j113-epass--permPass__form-page' + (parseInt($(this).attr('data-page')) - 1).toString()).removeClass('hidden');
            $('.m38-step-tracker').attr('data-current-step', (parseInt($(this).attr('data-page')) - 1).toString());
            $('.m38-step-tracker__progressbar').attr('aria-valuetext', step);
            jQuery(window).trigger('reinit_m38');
            $('.m13-footer-back-to-top__link').trigger('click');
        });
    };

    j113_epass.prototype.multiPassMobile = function() {
        var _this = this,
            $list = $('.j113-epass--records_list_wrapper');

        $('.j113-epass--select-mobile').html('');

        $list.find('button.j113-epass--records_item').each(function() {
            var $id = $(this).attr('data-emiratesID'),
                $name = $(this).find('.j113-epass--records_name').html(),
                $class = $(this).attr('class'),
                $status = $(this).find('.j113-epass--records_status').html(),
                $option = '<option class="' + $class + '" data-status="' + $status + '" value="' + $id + '">' + $name + '</option>';

            $('.j113-epass--select-mobile').append($option);
        });

        $('.form-field__input--select3').each(function() {
            var $parent = $(this).closest('.form-field');

            function selectFormatter(state) {
                //console.log($(state.element).attr('data-status'))

                if (!state.id) {
                    return state.text;
                }
                var $state = $(
                    '<div class="' + $(state.element).attr('class') + '"><div class="j113-epass--records_name">' + state.text + '</div><div class="j113-epass--records_status">' + $(state.element).attr('data-status') + '</div><div class="j113-epass--records_eid">EID: ' + state.element.value + '</div></div>'
                );
                return $state;
            };

            $(this).select2({
                width: '100%',
                templateResult: selectFormatter
            });

            $parent.find('.select2-selection__arrow').remove();
            $parent.find('.select2-selection').css('height', 'auto');
            $parent.find('.select2-selection__rendered').css({ 'padding': '5px 12px 8px', 'padding-right': '44px' });
        });

        $list.find('button.j113-epass--records_item').each(function() {
            if ($('.j113-epass--select-mobile').val() == $(this).attr('data-emiratesID')) {
                $(this).click();
            };
        });

        $('.j113-epass--select-mobile').off('change.mobile').on('change.mobile', function() {
            var selected = $(this).val();

            $list.find('button.j113-epass--records_item').each(function() {
                if (selected == $(this).attr('data-emiratesID')) {
                    $(this).click();
                };
            });
        });

    };

    j113_epass.prototype.multiPass = function() {
        var _this = this,
            $list = $('.j113-epass--records_list_wrapper'),
            $count = $('.j113-epass--records_count');

        $count.html($list.find('button.j113-epass--records_item').length);

        $list.find('button.j113-epass--records_item').each(function() {
            var $item = $(this),
                $remove = $item.find('.j113-epass--records_remove');

            $item.off('click.multi').on('click.multi', function(event) {
                var itemCheck = !$(this).hasClass('j113-epass--records_item--done') && !$(event.target).hasClass('j113-epass--records_remove'),
                    turnReadOnly = false;

                if (_this.$journey.hasClass('j113-epass--allow_view') && $(this).hasClass('j113-epass--records_item--done')) {
                    itemCheck = !$(event.target).hasClass('j113-epass--records_remove');
                    turnReadOnly = true;
                }

                if (itemCheck) {
                    var data = $(this).data(),
                        val_check = 0;

                    $list.find('button.j113-epass--records_item').each(function() {
                        $(this).removeClass('j113-epass--records_item--active');
                    });

                    $item.addClass('j113-epass--records_item--active');

                    for (var key in data) {
                        var keyS = key.toString().toLowerCase(),
                            value = data[key];

                        $('.j113-epass--multi_attachment').find('.form-field:not(.form-field--upload)').find('input').each(function() {
                            if ($(this).attr('name') != undefined) {
                                if ($(this).attr('name').toLowerCase() == keyS) {
                                    $(this).val(value);
                                    if ($(this).hasClass('form-field__input--datepicker') && (value.length > 0)) {
                                        $(this).pickadate('picker').set('select', value);
                                    }
                                    if (turnReadOnly) {
                                        $(this).addClass('disabled');
                                        $(this).prop('disabled', true);
                                    } else {
                                        $(this).removeClass('disabled');
                                        $(this).prop('disabled', false);
                                    }
                                };
                            };
                        });

                        $('.j113-epass--multi_attachment').find('.form-field:not(.form-field--upload)').find('textarea').each(function() {
                            if ($(this).attr('name') != undefined) {
                                if ($(this).attr('name').toLowerCase() == keyS) {
                                    $(this).val(value);
                                    if (turnReadOnly) {
                                        $(this).addClass('disabled');
                                        $(this).prop('disabled', true);
                                    } else {
                                        $(this).removeClass('disabled');
                                        $(this).prop('disabled', false);
                                    }
                                };
                            };
                        });

                        $('.j113-epass--multi_attachment').find('select').not('.picker__select--year, .picker__select--month').each(function() {
                            if ($(this).attr('name').toLowerCase() == keyS) {
                                if ($(this).find("option[value='" + value + "']").length) {
                                    $(this).val(value).trigger('change');
                                } else {
                                    // Create a DOM Option and pre-select by default
                                    var newOption = new Option(value, value, true, true);
                                    // Append it to the select
                                    $(this).append(newOption).trigger('change');
                                };
                                if (turnReadOnly) {
                                    $(this).addClass('disabled');
                                    $(this).prop('disabled', true);
                                } else {
                                    $(this).removeClass('disabled');
                                    $(this).prop('disabled', false);
                                }
                            };
                        });

                        $('.j113-epass--multi_attachment').find('.form-field--upload').each(function() {
                            if ($(this).find('.button--remove').css('display') == 'inline-block') {
                                $(this).find('.button--remove').click();
                                $(this).find('.parsley-errors-list').html('')
                            }
                            $(this).parsley().reset();
                        });
                    };
                    if ($('#form-field-checkbox_iswithcar').prop('checked')) {
                        $('.j113-epass--multi_attachment').find('.form-field:not(.form-field--upload)').find('input').each(function() {

                            if (!turnReadOnly) {
                                $(this).parsley().validate();

                                if (!$(this).parsley().isValid()) {
                                    val_check++
                                };
                            } else {
                                $(this).parsley().reset();
                                $(this).closest('.form-field__input-wrapper').removeClass('form-field__input-wrapper--validated');
                            };
                        });
                    } else {
                        $('.j113-epass--multi_attachment').find('.form-field:not(.form-field--upload,.withcar)').find('input').each(function() {

                            if (!turnReadOnly) {
                                $(this).parsley().validate();

                                if (!$(this).parsley().isValid()) {
                                    val_check++
                                };
                            } else {
                                $(this).parsley().reset();
                                $(this).closest('.form-field__input-wrapper').removeClass('form-field__input-wrapper--validated');
                            };
                        });
                    }

                    if (turnReadOnly) {
                        $('.m62-expand--item:last-child').hide();
                        $('#epasssubmitbutton').hide();
                    } else {
                        $('.m62-expand--item:last-child').show();
                        $('#epasssubmitbutton').show();
                        $(window).trigger('loadICAdetails');
                    }

                    //console.log(val_check)
                    $("html").animate({ scrollTop: 0 });

                    _this.checkFields(null, false)
                };
            });

            //$remove.on('click', function() {
            //    $item.remove();
            //    $count.html($list.find('button.j113-epass--records_item').length);
            //});

        });

    };

    j113_epass.prototype.initlaunchBar = function($bar) {
        var _this = this,
            correct = parseFloat($bar.attr('data-correct')),
            total = parseFloat($bar.attr('data-total')),
            greenBar = $bar.find('.j113-epass--permPass-green'),
            time = 0,
            rateNumber = 0,
            rateCorrect, rateStep;

        rateCorrect = parseFloat((correct / total) * 100).toFixed(2);
        greenBar.css('width', (rateCorrect).toString() + '%');

        rateStep = rateCorrect / 1500;

        for (var i = 0; i < 1500; i++) {
            setTimeout(function() {
                rateNumber = rateNumber + rateStep;
                $('.j113-epass--permPass-bar_rate').find('span').html(parseFloat(rateNumber).toFixed(2));
            }, time);
            time = time + 1;
        }
    };

    j113_epass.prototype.checkFields = function(event, isButton) {
        var _this = this,
            overError = 0;

        $('.j113-epass--multi_attachment').find('.m62-expand--content').each(function() {
            var $error = $(this).closest('.m62-expand--item').find('.m62-expand--error'),
                error = 0,
                $li = $(this);

            if ($('#form-field-checkbox_iswithcar').prop('checked')) {
                $(this).find('input, select').each(function() {
                    //$(this).parsley().validate()
                    if (!$(this).parsley().isValid() && $(this).closest('.form-field').css('display') != 'none') {
                        error++;
                        overError++;
                    };
                });
            } else {
                $(this).find('input, select').not('.withcar input, .withcar select').each(function() {
                    //$(this).parsley().validate()
                    if (!$(this).parsley().isValid() && $(this).closest('.form-field').css('display') != 'none') {
                        error++
                        overError++;
                    };
                });
            }

            if (error > 0) {
                if (!$error.closest('.m62-expand--trigger').hasClass('active'))
                    $error.fadeIn();
                if (isButton) {
                    if ($('#form-field-checkbox_iswithcar').prop('checked')) {
                        $(this).find('input, select').each(function() {
                            $(this).parsley().validate();
                        });
                    } else {
                        $(this).find('input, select').not('.withcar input, .withcar select').each(function() {
                            $(this).parsley().validate();
                        });
                    }
                    event.preventDefault();
                }
            } else {
                $error.fadeOut();
                //$('.j113-epass--multi_attachment').find('button[type="submit"]').off('click.disable');
            };
        });

        if (overError == 0 && isButton) {
            $(window).trigger('j113MultipassSubmit');
        }

    };

    j113_epass.prototype.loadMore = function(button, add) {
        var _this = this,
            increment = parseInt($(button).attr('data-increment')),
            $trs = $(button).closest('.m25-tabs__content').find('.j113-epass--pass_detail_card'),
            $trsmobile = $(button).closest('.m25-content--mobile').find('.j113-epass--pass_detail_card'),
            currentLoad = increment * _this.loadMultiplier,
            trCount;

        if ($(window).width() > 599) {
            trCount = $trs.length;
        } else {
            trCount = $trsmobile.length;
        }

        if (trCount <= (currentLoad)) {
            $(button).hide();
        }


        if ($(window).width() > 599) {
            $trs.each(function(index) {
                if (index < currentLoad) {
                    $(this).show();
                } else {
                    $(this).hide();
                }
            });
        } else {
            $trsmobile.each(function(index) {
                if (index < currentLoad) {
                    $(this).show();
                } else {
                    $(this).hide();
                }
            });
        }

        if (add) {
            _this.loadMultiplier = _this.loadMultiplier + 1;
        }

        $(button).off('click.load').on('click.load', function() {
            _this.loadMore(button, true);

        });
    };

    return j113_epass;
});

