define('../src/sublayouts/m62-expand/m62-expand',['jquery', 'breakpoint', 'lib/utils'], function($, breakpoint, utils) {

    'use strict';

    /**
     * This module only effects Tabs on desktop. Mobile view uses the toggle helper.
     */

    var Expand = function($component) {

        this.$component = $component;

        return this;
    };

    Expand.prototype.init = function() {

        $('.m62-expand--container .m62-expand--trigger').off('click.m62').on('click.m62', function(j) {
            var dropDown = $(this).closest('.m62-expand--item').find('.m62-expand--content');

            $(this).closest('.m62-expand--container').find('.m62-expand--content').not(dropDown).slideUp();

            if ($(this).hasClass('active')) {
                $(this).removeClass('active');
            } else {
                $(this).closest('.m62-expand--container').find('.m62-expand--trigger.active').removeClass('active');
                $(this).addClass('active');
            }

            dropDown.stop(false, true).slideToggle();

            j.preventDefault();
            $("html").animate({ scrollTop: 0 });
        });
    };

    return Expand;

});

