define('../src/journeys/j112-material-pass/j112-material-pass',['jquery', 'parsley'], function($, parsley) {

    'use strict';

    var j112 = function($journey) {

        this.$journey = $journey;

        return this;
    };

    j112.prototype.init = function() {
        var _this = this;

        this.editMode = false;
        this.currentRow = undefined;
        this.serial = 1;
        this.max = parseInt(this.$journey.attr('data-max-material'));
        this.label = JSON.parse(this.$journey.attr('data-labels'));

        this.initListeners();
        this.initTable();

        this.$journey.find('.m39-modal--form').find('[data-close="true"]').off('click.editMode').on('click.editMode', function() {
            _this.editMode = false;
        });

    };

    j112.prototype.initListeners = function() {
        var _this = this,
            $modal = this.$journey.find('.m39-modal--form'),
            $mClose = $modal.find('[data-close="true"]'),
            $table = this.$journey.find('.j112-material-table'),
            $mTrigger = this.$journey.find('.m39-modal--form').find('.m39-modal__trigger');

        if ($table.find('.j113-epass--table_row').length == 0) {
            $table.hide();
            $('.j112-material--placeholder').show();
        } else {
            $table.show();
            $('.j112-material--placeholder').hide();
        }

        $mClose.off('click.j112').on('click.j112', function() {
            setTimeout(function() {
                $modal.find('input, textarea').each(function() {
                    $(this).parsley().reset();
                    $(this).val('')
                    $(this).removeClass('form-field__input--error');
                    $(this).closest('.form-field__input-wrapper').removeClass('form-field__input-wrapper--validated').removeClass('form-field__input-wrapper--error');
                });
            }, 500);
        });

        this.$journey.find('.j112-material--trigger').off('click.j122').on('click.j122', function() {
            if (_this.max > $table.find('.j113-epass--table_row').length) {
                $mTrigger.trigger('click');
                $('.j112-material-pass--limit').hide();
            } else {
                $('.j112-material-pass--limit').show();
            }

        });

        this.$journey.find('.j112-material-editTable').each(function() {
            $(this).off('click.j112').on('click.j112', function() {
                var $row = $(this).closest('.j113-epass--table_row'),
                    $nameInput = $modal.find('[name="material-name"]'),
                    $descInput = $modal.find('[name="material-desc"]'),
                    $inwardInput = $modal.find('[name="material-quantity"]');

                $nameInput.val($row.find("[data-material]").html().trim());
                $descInput.val($row.find("[data-description]").html().trim());
                $inwardInput.val($row.find("[data-quantity]").html().trim());

                $mTrigger.trigger('click');
                _this.editMode = true;
                _this.currentRow = $row;
            });
        });

        this.$journey.find('.j112-material-removeRow').each(function() {
            $(this).off('click.j112').on('click.j112', function() {
                _this.$journey.find('.j112-material-tableMobile_item[data-serial="' + $(this).closest('.j113-epass--table_row').find('.j113-epass--table_cell[data-serial]').attr('data-serial') + '"]').remove();

                $(this).closest('.j113-epass--table_row').remove();

                _this.$journey.find('.j113-epass--table_row').find('.j113-epass--table_cell[data-serial]').each(function(index) {
                    $(this).attr('data-serial', (index + 1).toString());
                    $(this).html((index + 1).toString());
                });

                _this.$journey.find('.j112-material-tableMobile_item').each(function(index) {
                    $(this).attr('data-serial', (index + 1).toString());
                    $(this).find('.j112-material-tableMobile_serial').html((index + 1).toString());
                });

                _this.serial = _this.serial - 1;

                if ($table.find('.j113-epass--table_row').length == 0) {
                    $table.hide();
                    $('.j112-material--placeholder').show();
                } else {
                    $table.show();
                    $('.j112-material--placeholder').hide();
                }
            });
        });

        this.$journey.find('.j112-material-tableMobile_header').each(function() {
            $(this).off('click.j112').on('click.j112', function() {
                $(this).closest('.j112-material-tableMobile_item').toggleClass('open');
            });
        });

        this.$journey.find('.j112-material-editTable_mobile').each(function() {
            $(this).off('click.j112').on('click.j112', function() {
                var i = $(this).closest('.j112-material-tableMobile_item').attr('data-serial'),
                    $desktopEdit = _this.$journey.find('.j113-epass--table_cell[data-serial="' + i + '"]').closest('.j113-epass--table_row').find('.j112-material-editTable');

                $desktopEdit.trigger('click');
            });
        });

        this.$journey.find('.j112-material-removeRow_mobile').each(function() {
            $(this).off('click.j112').on('click.j112', function() {
                var i = $(this).closest('.j112-material-tableMobile_item').attr('data-serial'),
                    $desktopRemove = _this.$journey.find('.j113-epass--table_cell[data-serial="' + i + '"]').closest('.j113-epass--table_row').find('.j112-material-removeRow');

                $desktopRemove.trigger('click');
                //$(this).closest('.j112-material-tableMobile_item').remove();
            });
        });
    };

    j112.prototype.initTable = function() {
        var _this = this,
            $modal = this.$journey.find('.m39-modal--form'),
            $nameInput = $modal.find('[name="material-name"]'),
            $descInput = $modal.find('[name="material-desc"]'),
            $inwardInput = $modal.find('[name="material-quantity"]'),
            $button = $modal.find('.j112-material-tableUpdate'),
            $mobileTable = this.$journey.find('.j112-material-tableMobile'),
            $table = this.$journey.find('.j112-material-table');

        $modal.find('[data-close="true"]').off('click.j112').on('click.j112', function() {
            $modal.find('.form-field__input-wrapper').removeClass('form-field__input-wrapper--error');
            $modal.find('.form-field__input').removeClass('form-field__input--error');
            $modal.find('.form-field__input').each(function () {
                $(this).parsley().reset();
            });
            //$modal.find('.form-field__messages').html('');
            $modal.find('.form-field--either').find('.form-field').last().find('.form-field__messages').html('');
        });

        $button.off('click.j112').on('click.j112', function() {
            var errors = 0,
                either = 0;

            $modal.find('input, textarea').each(function() {
                $(this).parsley().validate();
                if (!$(this).parsley().isValid()) {
                    errors++
                }
            });

            $modal.find('.form-field--either').find('input').each(function() {

                if ($(this).val().length != 0) {
                    either++
                };
            });

            if (either == 0) {
                errors++;
                $(this).closest('.form-field__input-wrapper').removeClass('form-field__input-wrapper--validated').removeClass('form-field__input-wrapper--error');
                $modal.find('.form-field--either').find('.form-field').last().find('.form-field__messages').html('<ul class="parsley-errors-list filled" id="parsley-id-16"><li class="parsley-required">' + _this.$journey.attr('data-eithertext') + '</li></ul>')
            } else {
                $modal.find('.form-field--either').find('.form-field').last().find('.form-field__messages').html('');
            }

            if (!_this.editMode && errors == 0) {
                $table.find('tbody').append('<tr class="j113-epass--table_row"><td class="j113-epass--table_cell" data-serial="' + _this.serial.toString() + '">' + _this.serial.toString() + '</td><td class="j113-epass--table_cell" data-material="">' + $nameInput.val() + '</td><td class="j113-epass--table_cell" data-description="">' + $descInput.val() + '</td><td class="j113-epass--table_cell" data-quantity="">' + $inwardInput.val() + '</td><td class="j113-epass--table_cell"><div class="link j112-material-editTable">' + _this.$journey.attr('data-editLabel') + '</div><div class="link red j112-material-removeRow">' + _this.$journey.attr('data-removeLabel') + '</div></td></tr>')

                $mobileTable.append('<div class="j112-material-tableMobile_item" data-serial="' + _this.serial.toString() + '"><div class="j112-material-tableMobile_header"><span class="j112-material-tableMobile_serial">' + _this.serial.toString() + '</span>. ' + $nameInput.val() + '</div><div class="j112-material-tableMobile_content"><div class="j112-material-tableMobile_tr"><div class="j112-material-tableMobile_trHead">' + _this.label.material + '</div><div class="j112-material-tableMobile_trCell">' + $nameInput.val() + '</div></div><div class="j112-material-tableMobile_tr"><div class="j112-material-tableMobile_trHead">' + _this.label.desc + '</div><div class="j112-material-tableMobile_trCell">' + $descInput.val() + '</div></div><div class="j112-material-tableMobile_tr"><div class="j112-material-tableMobile_trHead">' + _this.label.quantity + '</div><div class="j112-material-tableMobile_trCell">' + $inwardInput.val() + '</div></div><div class="j112-material-tableMobile_tr"><div class="j112-material-tableMobile_trHead">' + _this.label.action + '</div><div class="j112-material-tableMobile_trCell"><div class="link j112-material-editTable_mobile">' + _this.$journey.attr('data-editLabel') + '</div><div class="link red j112-material-removeRow_mobile">' + _this.$journey.attr('data-removeLabel') + '</div><td></div></div></div></div>')

                $modal.find('[data-close="true"]').trigger('click');
                _this.initListeners();
                _this.serial++;
            } else if (_this.editMode && errors == 0) {
                _this.currentRow.html('<td class="j113-epass--table_cell" data-serial="' + _this.serial.toString() + '">' + _this.currentRow.find('[data-serial]').html() + '</td><td class="j113-epass--table_cell" data-material="">' + $nameInput.val() + '</td><td class="j113-epass--table_cell" data-description="">' + $descInput.val() + '</td><td class="j113-epass--table_cell" data-quantity="">' + $inwardInput.val() + '</td><td class="j113-epass--table_cell"><div class="link j112-material-editTable">' + _this.$journey.attr('data-editLabel') + '</div><div class="link red j112-material-removeRow">' + _this.$journey.attr('data-removeLabel') + '</div></td>');

                _this.$journey.find('.j112-material-tableMobile_item[data-serial="' + _this.currentRow.find('[data-serial]').html() + '"]').html('<div class="j112-material-tableMobile_header"><span class="j112-material-tableMobile_serial">' + _this.currentRow.find('[data-serial]').html() + '</span>. ' + $nameInput.val() + '</div><div class="j112-material-tableMobile_content"><div class="j112-material-tableMobile_tr"><div class="j112-material-tableMobile_trHead">' + _this.label.material + '</div><div class="j112-material-tableMobile_trCell">' + $nameInput.val() + '</div></div><div class="j112-material-tableMobile_tr"><div class="j112-material-tableMobile_trHead">' + _this.label.desc + '</div><div class="j112-material-tableMobile_trCell">' + $descInput.val() + '</div></div><div class="j112-material-tableMobile_tr"><div class="j112-material-tableMobile_trHead">' + _this.label.quantity + '</div><div class="j112-material-tableMobile_trCell">' + $inwardInput.val() + '</div></div><div class="j112-material-tableMobile_tr"><div class="j112-material-tableMobile_trHead">' + _this.label.action + '</div><div class="j112-material-tableMobile_trCell"><div class="link j112-material-editTable_mobile">' + _this.$journey.attr('data-editLabel') + '</div><div class="link red j112-material-removeRow_mobile">' + _this.$journey.attr('data-removeLabel') + '</div><td></div></div></div>')

                $modal.find('[data-close="true"]').trigger('click');
                _this.initListeners();
                _this.editMode = false;
                _this.currentRow = undefined;
            };

            $modal.find('.form-field--either').off('keypress.j112').on('keypress.j112', function() {
                var errors = 0,
                    either = 0;

                $modal.find('input, textarea').each(function() {
                    $(this).parsley().validate();
                    if (!$(this).parsley().isValid()) {
                        errors++
                    }
                });

                $modal.find('.form-field--either').find('input').each(function() {

                    if ($(this).val().length != 0) {
                        either++
                    };
                });

                if (either == 0) {
                    errors++;
                    $(this).closest('.form-field__input-wrapper').removeClass('form-field__input-wrapper--validated').removeClass('form-field__input-wrapper--error');
                    $modal.find('.form-field--either').find('.form-field').last().find('.form-field__messages').html('<ul class="parsley-errors-list filled" id="parsley-id-16"><li class="parsley-required">' + _this.$journey.attr('data-eithertext') + '</li></ul>')
                } else {
                    $modal.find('.form-field--either').find('.form-field').last().find('.form-field__messages').html('');
                };

            });

        });

        j112.prototype.initTableMobile = function() {
            var _this = this,
                $table = this.$journey.find('.j112-material-table');


        };
    };

    return j112;
});

